$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  display: block;
}

:host {
  --mt-container-padding-x: 24px;

  --mt-container-xxl-max-width: 1200px;
  --mt-container-xl-max-width: 100%;
  --mt-container-lg-max-width: 100%;
  --mt-container-md-max-width: 100%;
  --mt-container-sm-max-width: 100%;

  --mt-container-background-color: null;
  background-color: var(--mt-container-background-color);
  width: 100%;

  padding-right: calc(
    var(--mt-container-padding-right, var(--mt-container-padding-x)) * 0.5
  );
  padding-left: calc(
    var(--mt-container-padding-left, var(--mt-container-padding-x)) * 0.5
  );
  margin-right: auto;
  margin-left: auto;

  @each $mt-breakpoint-key, $mt-breakpoint-value in $mt-breakpoints {
    @media (min-width: $mt-breakpoint-value) {
      max-width: var(--mt-container-#{$mt-breakpoint-key}-max-width);
    }
  }
}
